/************** SearchPage **************/

#searchPage {
    width: 100%;
    min-width: 320px;
    max-width: var(--max-width);
    margin: auto;
    transition: bottom 200ms, opacity 250ms;
  }
  #createNewBG {
    position: fixed;
    width: 100%;
    height: 100%;
    background-color: var(--background-kinda-white);
    z-index: 1;
    display: none;
    opacity: 0;
    transition: opacity 200ms;
  }
  #createNewContainer {
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
    width: 300px;
    background-color: var(--background-white);
    border-radius: 15px;
    z-index: 2;
    display: none;
    opacity: 0;
    box-shadow: 0px 0px 10px -3px rgba(0, 0, 0, 0.55);
    transition: opacity 200ms;
  }
  #createNewContainer p {
    text-align: center;
    color: white;
    background-color: var(--main-color);
    background: linear-gradient(90deg, var(--main-color) 0%, var(--second-color) 75%);
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    border-radius: 10px;
    padding: 10px;
    margin: 40px 15px;
    box-shadow: 0px 1px 10px -7px rgba(0, 0, 0, 0.55);
    cursor: pointer;
  }
  #createNewContainer p:hover {
    background-color: var(--second-color);
    background: linear-gradient(90deg, var(--second-color) 0%, var(--main-color) 75%);
  }
  #searchPageHeader {
    position: sticky;
    top: 0;
    width: 100%;
    height: 150px;
    background-color: var(--main-color);
  }
  #searchPageHeaderTopContainer {
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 90%;
    margin: auto;
    padding: 10px 0;
  }
  #searchPageBack {
    width: 35px;
    height: 35px;
    fill: white;
    cursor: pointer;
  }
  #searchPageBack:hover {
    fill: black;
  }
  #searchPageHeaderTitle {
    text-align: right;
    color: white;
    margin: 0;
    font-size: calc(18px + 6 * ((var(--vw) - 320px) / 680));
    font-weight: bold;
    line-height: 40px;
  }
  #searchBar {
    display: block;
    margin: auto;
    width: 90%;
    height: 45px;
    margin-top: 15px;
    padding-right: 50px;
    padding-left: 10px;
    border: none;
    border-radius: 10px;
    /* box-shadow: 0px 0px 10px 0px #00000040; */
    background-color: var(--background-white);
    background-image: url(../assets/search.png);
    background-repeat: no-repeat;
    background-size: 20px 20px;
    background-position: calc(100% - 20px) 50%;
    font-size: 18px;
    color: var(--text-kinda-black);
    transition: all 500ms;
  }
  #searchBar::placeholder {
    font-size: 18px;
  }
  #searchBar:focus {
    transform: scale(1.03);
    box-shadow: 0px 0px 10px 0px #00000040;
  }
  #searchOption {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-template-rows: repeat(2, 1fr);
    grid-column-gap: 10px;
    grid-row-gap: 10px;
    margin: 10px;
    border-radius: 10px;
  }
  #searchOption p {
    display: grid;
    padding: 15%;
    justify-items: center;
    align-self: stretch;
    justify-self: stretch;
    margin: 0;
    cursor: pointer;
    font-weight: bold;
    background-color: var(--background-white);
    font-size: calc(15px + 6 * ((var(--vw) - 320px) / 680));
    color: var(--text-kinda-black);
    border-radius: 3px;
  }
  #searchOption p:hover {
    background-color: var(--main-color);
    color: white;
  }
  #searchOption svg {
    margin: 15px 0;
    fill: var(--main-color);
  }
  #searchOption p:hover svg {
    fill: white;
  }
  .searchResultsTitles {
    margin: 3px 20px;
    color: var(--text-kinda-black);
    font-size: 15px;
  }
  #searchNoResults {
    text-align: center;
    color: var(--text-kinda-black);
    margin-top: 50px;
  }
  #lottieSearchNoResults {
    width: 50%;
    display: none;
    margin: auto;
  }
  @media (max-width: 450px) {
    #searchPageBack {
      width: 30px;
      height: 30px;
    }
  }
  